:root {
  --main-bg-color: #00a0ff;
}

/* importing fonts */
@font-face {
  font-family: "Karla";
  src: url("./fonts/karla-regular-webfont.woff2") format("woff2"),
    url("./fonts/karla-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Karla";
  src: url("./fonts/karla-italic-webfont.woff2") format("woff2"),
    url("./fonts/karla-italic-webfont.woff") format("woff");
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: "Karla";
  src: url("./fonts/karla-bold-webfont.woff2") format("woff2"),
    url("./fonts/karla-bold-webfont.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "Karla";
  src: url("./fonts/karla-bolditalic-webfont.woff2") format("woff2"),
    url("./fonts/karla-bolditalic-webfont.woff") format("woff");
  font-weight: bold;
  font-style: italic;
}

/* normalize css */
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1.2;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* finish normalize css */

/* LOGIN SCREEN STYLES */
.app {
  font: normal 1em/1.3 "Karla", sans-serif;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: #ccc;
}

.container {
  font: normal 1em/1.3 "Karla", sans-serif;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: #ccc;
}

.fullScr {
  height: 70%;
  min-height: 41.6875em;
  width: 23.4375em;
  background-color: #00a0ff;
  position: relative;
}

.fullScr__firstBgBlock {
  background-color: #00a0ff;
  width: 100%;
  height: 56.522%;
}

.fullScr__secondBgBlock {
  background-color: #000;
  width: 100%;
  height: 43.478%;
}

.fullScr__appName {
  position: absolute;
  font-weight: bold;
  font-size: 625%;
  color: #fff;
  letter-spacing: -1px;
  top: 8%;
  left: -3%;
  z-index: 2;
}

.fullScr__chatIcon {
  margin: auto;
  position: absolute;
  top: -38%;
  left: 0;
  bottom: 0;
  right: 0;
  height: 19%;
}

.fullScr__loginFormWrapper {
  margin: auto;
  position: absolute;
  top: 39%;
  left: 0;
  bottom: 0;
  right: 0;
  width: 87%;
  height: 41%;
}

.loginForm {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  padding: 11% 12%;
}

.loginForm__label {
  text-transform: uppercase;
  font-size: 62.5%;
  color: #ad807d;
  margin-bottom: -31.25%;
  padding: 0;
  letter-spacing: 2px;
  font-weight: bold;
}

.loginForm__label_alarm {
  color: #ff0000;
}
.loginForm__input {
  font-size: 87.5%;
  color: #000;
  width: 100%;
  padding: 4% 0;
  margin-bottom: 10%;
  display: block;
  border: none;
  border-bottom: solid 1px rgba(0, 0, 0, 0.2);
}

.loginForm__input:focus {
  outline: none;
  border-bottom: solid 2px #00a0ff;
}

#userName.loginForm__input_alarm {
  border-bottom: solid 2px #ff0000;
}

.loginForm__submit {
  border: none;
  font-size: 81.25%;
  font-weight: bold;
  color: #fff;
  position: absolute;
  left: 64%;
  bottom: 8%;
  z-index: 1000;
  background-color: #00a0ff;
}
.loginForm__submit:focus {
  font-size: 88%;
}
.loginForm__submitBg {
  position: absolute;
  width: 100%;
  height: 22.5%;
  background-color: #00a0ff;
  bottom: 0;
  left: 0;
}

.footerText {
  color: #fff;
  font-size: 68.75%;
}

.fullScr__footerTextWrapper {
  position: relative;
  text-align: center;
  top: -7%;
}

.footerText__link {
  text-decoration: none;
  border-bottom: 1px #fff solid;
  padding: 1.5% 0;
  color: #fff;
}

.footerText__link:visited {
  color: #fff;
}

.footerText__link:focus {
  font-size: 110%;
  outline: none;
}

/* END OF LOGIN SCREEN STYLES */

/* MESSAGES SCREEN STYLES */

.screen {
  height: 70%;
  min-height: 41.6875em;
  width: 23.4375em;
  background-color: #00a0ff;
  position: relative;
  display: -ms-flexbox;
  display: flex;
}

/* .elContainer {
  display: contents;
} */

.screen__header {
  position: absolute;
  width: 100%;
  top: 0;
  background-color: #000;
  height: 4rem;
  font-size: 1.0625em;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.screen__msgInputForm {
  position: absolute;
  width: 100%;
  bottom: 0;
  background-color: #000;
  height: 3.43rem;
  font-size: 1.0625em;
  color: #fff;
}

.msgInputForm__sendField {
  position: absolute;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #000;
  color: #fff;
  font-size: 0.9375rem;
  padding-left: 4.0625rem;
  padding-right: calc(1.5625em + 60px + 1.5625em);
  border: none;
  height: 3.43rem;
  width: 100%;
}

.msgInputForm__sendField:focus {
  outline: none;
  border-bottom: solid 2px #00a0ff;
}

.screen__sendButton {
  position: absolute;
  bottom: 1.8em;
  /* left:76%; */
  right: 1.5625em;
  z-index: 5;
  background: none;
  border: none;
}

.screen__sendButton_noFocus:focus {
  outline: none;
}

.screen__chatArea {
  margin-top: 4rem;
  padding: 0 1.5625em;
  height: calc(100% - 7.43rem);
  width: 100%;
  /* background-color: orange; */
  overflow: auto;
}

.clearfix {
  display: table;
  content: "";
  clear: both;
}

.chatArea__fullMsgContainer {
  margin: 1em auto;
}

.chatArea__data {
  text-align: center;
  margin: 0 auto;
  padding: 1.5em;
  font-size: 0.6875rem;
  text-transform: uppercase;
  color: #fff;
  font-weight: bold;
}

.chatArea__data_firstData {
  margin-top: 1em;
}

.chatArea__msgBlockContainer {
  margin: 0.75em 0;
}

.chatArea__msgBlockContainer_owner {
  float: right;
}

.chatArea__msgBlockContainer_otherUser {
  float: left;
}

.msgBlock {
  position: relative;
  padding: 1.5rem;
  /* margin: 14px 25px 25px 25px; */
  background-color: #fff;
  color: #000;
  font-size: 0.9375rem;
  -webkit-box-shadow: 0 14px 28px rgba(0, 0, 0, 0.15), 0 10px 10px rgba(0, 0, 0, 0.1);
          box-shadow: 0 14px 28px rgba(0, 0, 0, 0.15), 0 10px 10px rgba(0, 0, 0, 0.1);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 15.625em;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-all;
  word-break: break-word;
  float: right;
}

.msgBlock__text {
  color: #000;
  font-size: 0.9375rem;
  line-height: 1.4375rem;
}

.msgBlock__attrs {
  float: right;
}
.attrs__close {
  position: absolute;
  top: 0.875em;
  right: 0.625em;
  height: 12px;
}

.attrs__name {
  color: #ad807d;
  font-weight: bold;
  font-size: 0.75rem;
  text-transform: capitalize;
}

.attrs__time {
  color: #ad807d;
  font-weight: bold;
  font-size: 0.625rem;
}

/* END OF MESSAGES SCREEN STYLES */

@media (max-width: 450px) {
  .fullScr {
    height: 100%;
    width: 100%;
  }

  .screen {
    min-height: 100%;
    width: 100%;
    min-width: 100vw;
  }
}



